import { WatchlistDataTablePercentageCell } from "../../WatchlistDataTableCells/WatchlistDataTablePercentageCell"
import { genericColumn } from "../../genericColumn"

function capexToRevenueTtm(tableName: string) {
  const title = "Capex/Sales (LTM)"
  return genericColumn({
    endpoint: "companyKeyMetrics",
    columnKeys: ["capexToRevenueTtm"],
    size: 110,
    title,
    enableSorting: true,
    enableHiding: true,
    tooltip: "Capex as a portion of sales.",
    cell: info => {
      return (
        <WatchlistDataTablePercentageCell
          tooltip={`Explain the ${title}`}
          queryResult={info.row.original.financialData.companyKeyMetrics}
          onClick={() => {
            info.row.original.onChatWithCell?.({
              prompt: `What has ${info.row.original.entityInfo.name} recently reported about capex to sales or other relevant financial measures?`,
              value: info.row.original.financialData.companyKeyMetrics?.data?.capexToRevenueTtm,
              entityInfo: info.row.original.entityInfo,
              columnName: `${tableName}: ${title}`,
            })
          }}
          value={
            (info.row.original.financialData.companyKeyMetrics?.data?.capexToRevenueTtm ?? 0) * 100
          }
        />
      )
    },
  })
}

export { capexToRevenueTtm }
